$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "vars";

@function persent($px, $base: $base-width) {
	@return $px/$base*100 + %; }

@function vw($px, $base: 1920) {
	@return $px/$base*100 + vw; }

@function vh($px, $base: 972) {
	@return $px/$base*100 + vh; }

$base-color: #171717;
$border-color: #d5dbdf;
$error-color: #ff0024;

.fancybox-close {
	background: url(../img/fancybox-close.png) no-repeat center; }

.fancybox {

	&-skin {
		border-radius: 0; }

	&-close {
		@include size(30px);
		position: absolute;
		top: 50px;
		right: 55px;
		text-decoration: none;
		display: flex;
		align-items: center;
		justify-content: center;

		&:after {
			//content: "+"
			color: #000;
			font-weight: 300;
			font-size: 40px;
			display: block;
			transform: rotate(45deg); } } }

.forms__input_error {

	~ .forms__error-message {
		display: block; } }

.forms {
	display: none;
	box-sizing: border-box;
	padding: 4.4rem;
	background: #fff;

	&__title {
		font-size: rem(30);
		text-align: center;
		font-family: "Proxima Nova", sans-serif;
		font-weight: 300;
		margin-bottom: 55px;
		color: #000; }

	&-header {
		border-bottom: 1px solid $border-color; }

	&-text-cont {
		display: flex;
		justify-content: center;
		font-size: rem(14);
		line-height: rem(20);
		letter-spasing: 0.04rem; }

	&__text {
		margin: vh(25) 0;
		max-width: 540px;
		text-align: center;
		font-size: rem(14);
		line-height: rem(20); }

	&-wrap {
		display: flex;
		flex-wrap: wrap;
		margin: 0 0 -18px -30px;

		&--bot {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 25px; }

		&--middle {
			color: #000;
			font-size: rem(14); } }

	&-input-cont {
		display: flex;
		flex-direction: column-reverse;
		box-sizing: border-box;
		justify-content: space-between;
		position: relative;
		width: calc((100% - 30px * 2) / 2);
		margin: 0 0 24px 30px;

		&--req {
			justify-content: space-between;
			align-items: flex-start; }

		&--file, &--textarea {
			width: 100%;
			padding: 0 !important; }

		&--file {
			position: relative; } }

	&__input {
		box-sizing: border-box;
		@include size(100% 52px);
		border: 1px solid $border-color;
		padding: 0 20px;
		outline: none;

		&_error {
			border-color: $error-color;

			+ .forms__checkbox-label {
				color: $error-color; } }

		&[required] + .forms__label:after {
			content: "*";
			color: red; }

		&--select {
			-webkit-appearance: none;
			background: url("../img/ico-select-arrow.png") center right 15px no-repeat, #f4f4f4;
			color: #777777; }

		&--file {
			display: none;

			&-support {
				width: 77.34%; } }

		&--textarea {
			max-width: 100%;
			width: 100% !important;
			height: 140px;
			padding: {
				top: 5px;
				bottom: 5px; }
			max-height: 300px;
			min-height: 140px;
 } }			// resize: none

	&__file-formats {
		order: -1;
		margin-top: vh(15);
		font-size: rem(14);
		color: #777777; }

	&__label {
		margin-bottom: 10px;
		color: #000;
		font-size: rem(16);
		font-weight: 300;

		&--file {
			order: 1;
			pointer-events: none;

			&:before {
				content: attr(data-btn-text);
				position: absolute;
				pointer-events: all;
				background: url("../img/ico-view.png") center left 15px no-repeat, #f4f4f4;
				right: 0;
				top: 33px;
				cursor: pointer;
				@include size(18.57% 49px);
				text-align: right;
				line-height: 49px;
				box-sizing: border-box;
				padding-right: vw(16); } } }

	&-checkbox {
		position: relative;

		&:before {
			content: "";
			position: absolute;
			top: 3px;
			left: 0;
			@include size(23px);
			//border: 1px solid $border-color
			border-radius: 1px;
			background: #fff; } }

	&__checkbox {
		opacity: 0;
		position: absolute;

		&:checked + label {

			&:before, &:after {
				opacity: 1; } }

		&-label {
			font-size: rem(16);
			line-height: rem(20);
			padding-left: 45px;
			cursor: pointer;
			height: 32px;
			display: flex;
			span {
				align-self: center; }

			&:before, &:after {
				content: "";
				position: absolute;
				background: #e31e24;
				transition: opacity .5s ease;
				opacity: 0; }

			&:before {
				@include size(10px 2px);
				top: 11px;
				left: 5px;
				transform-origin: left;
				transform: rotate(45deg); }

			&:after {
				@include size(10px 2px);
				top: 11px;
				left: 8px;
				transform-origin: right;
				transform: rotate(-45deg); } } }

	&__req-text {
		text-align: right;

		&:before {
			content: "* ";
			color: red;
			font-size: rem(14); } }

	&-submit-cont {
		display: flex;
		justify-content: center; }

	&__submit {
		color: #fff;
		background: $main-color;
		border: 0;
		cursor: pointer;
		font-size: rem(16);
		line-height: 1;
		padding: 13px 34px 15px;
		border-radius: 30px; }

	&__error {

		&-message {
			border: 1px solid #d4d4d4;
			background: #fff;
			color: #000;
			padding: 7px 10px;
			border-radius: 7px;
			position: absolute;
			bottom: 45px;
			right: -6px;
			box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.1);
			display: none;

			&:after {
				content: "";
				background: url("../img/error-triangle.png") no-repeat;
				position: absolute;
				top: calc(100% - 0px);
				right: 19px;
				@include size(15px 8px); }

			&--file {
				bottom: initial;
				right: 21%;
				top: 0; }

			&-icon {
				display: block;
				border-radius: 50%;
				position: absolute;
				border: 1px solid $error-color;
				@include size(19px);
				background: #fff;
				bottom: -32px;
				right: 16px;

				&:after, &:before {
					content: "";
					position: absolute;
					width: 1px;
					background: $error-color;
					left: calc(50% - 1px); }

				&:before {
					top: 4px;
					height: 8px; }

				&:after {
					bottom: 4px;
					height: 2px; } } } }
	&-input-link {
		padding: 16px 18px;
		background: #f2f6f8;
		text-decoration: none;
		color: #000; } }








@media screen and (max-width: 767px) {
	.forms-wrap--middle {
		max-width: 360px; }
	.fancybox-close {
		right: 10px;
		top: 10px; }
	.forms {
		&-input-cont {
			width: 100%; }
		&__label {
			&--file {

				&:before {
					background-image: none;
					text-align: center;
					padding-right: 0; } } }

		&-wrap {

			&--bot {

				> div {
					width: 100%;

					&:not(:first-child) {
						margin-top: vh(15); } } } }

		&-checkbox {}

		&__checkbox {
			width: 100%; }

		&__req-text {
			text-align: center; } }


	.forms-input-cont {
		flex-direction: column-reverse; }

	.forms {
		&__label {
			margin-bottom: vh(10); }
		&-wrap--bot {
			flex-direction: column; } } }



@media screen and (max-width: 660px) {


	.fancybox-close {
		top: 0;
		right: 0;
		background: url(../img/close-w.svg) no-repeat center, $main-color;
		background-size: 80% !important; } }

